export default function Uploader(props) {
    const classes = useStyles();
    const history = useHistory();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [tempFile, setTempFile] = useState(null);
    const [loadingDialogOpen, setLoadingDialogOpen] = useState(false);
    const [fileDialogOpen, setFileDialogOpen] = useState(false);
    const [uploadState, setUploadState] = useState({
        upload: false,
        ocr: false
    });
    const [fileList, setFileList] = useState(null);
    const [docList, setDocList] = useState(null);

    const handleFile = (e) => {
        e.preventDefault();
        setTempFile(e.target.files[0]);
    }

    const submitFile = (e) => {
        e.preventDefault();
        if (tempFile !== null) {
            setLoadingDialogOpen(true);
            axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "UPLOAD",
                "file": tempFile.name
            })
            .then((res)=> {
                console.log(res)
                if (res.data.body.result === "OK") {
                    axios.put(res.data.body.upload_url, tempFile)
                    .then((res) => {
                        console.log(res)
                        setUploadState({...uploadState, upload: true});
                        axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                            "action": "OCR",
                            "file": tempFile.name
                        }, {headers : {"InvocationType":"Event"}})
                        .then((res) => {
                            console.log(res)
                            const timeout = setTimeout(() => {
                                setLoadingDialogOpen(false);
                                clearInterval(timer)
                                return Promise.reject("Timeout ERROR")
                            }, 120000)
                            const timer = setInterval(() => {
                                axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                                    "action": "STATUS",
                                    "file": tempFile.name
                                })
                                .then((res) => {
                                    console.log(res)
                                    if (res.data.body.result === "OK") {
                                        if (res.data.body.status === "READY") {
                                            clearInterval(timer);
                                            clearTimeout(timeout);
                                            setUploadState({...uploadState, ocr: true});
                                            setLoadingDialogOpen(false);
                                            enqueueSnackbar("  OCR ", {variant: 'success'});
                                            loadFileList();
                                            setUploadState({...uploadState, ocr: false});
                                        } else if (res.data.body.status === "ERROR") {
                                            return Promise.reject(res.data.body.message);
                                        }
                                    } else {
                                        return Promise.reject(res.data.body.message);
                                    }
                                })
                            }, 5000);
                            
                            
                            // 2 min   
                        })
                    })
                } else return Promise.reject(res.data.body.message);
                
            })
            .catch((err)=> {
                console.log(err);
                setLoadingDialogOpen(false);
                enqueueSnackbar('  : '+err, {variant: 'error'});
            })
        } else {
            enqueueSnackbar('  ', {variant: 'error'});
        }
    }

    const loadFileList = () => {
        axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "FLIST"
            })
            .then((res)=> {
                console.log(res.data.body.data)
                setFileList(res.data.body.data)
            })
            .catch((err)=> {
                console.log(err);
                enqueueSnackbar(' ', {variant: 'error'});
            });
    }

    const loadDocList = (fileId, fileName) => {
        enqueueSnackbar('Loading...');
        props.setFileName(fileName)
        axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "WPLIST",
                "file_id": fileId
            })
            .then((res)=> {
                console.log(res.data.body.data);
                setDocList(res.data.body.data);
                closeSnackbar();
                setFileDialogOpen(true);
            })
            .catch((err)=> {
                console.log(err);
                enqueueSnackbar(' ', {variant: 'error'});
            })
    }

    const loadOcrData = (wpsId) => {
        enqueueSnackbar('Loading...');
        axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "WEDIT",
                "wps_id": wpsId
            })
            .then((res)=> {
                console.log(res.data);
                if (res.data.body.result === "OK") {
                    props.setOcrData(res.data.body.data);
                    props.setFileURL(res.data.body.download_url);
                    history.push('/inspect');
                } else {
                    return Promise.reject(res.data.body.message)
                }
            })
            .catch((err)=> {
                console.log(err);
                enqueueSnackbar('  : '+err, {variant: 'error'});
            })
    }



    const InspectBtn = (props) => {
        return (
        <Button className={props.disabled ? classes.disabledBtn : classes.inspectBtn} disabled={props.disabled}>
            
        </Button>)
    }

    const EditBtn = (props) => {
        return (
        <Button className={props.disabled ? classes.disabledBtn : classes.editBtn} disabled={props.disabled} onClick={() => loadOcrData(props.wpsId)}>
            
        </Button>)
    }

    const showBtn = (status, wpsId, fileName) => {
        switch (status) {
            case "READY":
                return (<EditBtn wpsId={wpsId}/>)
            case "INPSECT":
            case "COMPLETE":
                return (<InspectBtn />)
            default:
                return (<Button className={classes.disabledBtn}></Button>)
        }
    }

    useEffect(() => {
        loadFileList();

    }, [])

    return (
        <>
        <div className={classes.uploader}>
            <Box fontWeight="fontWeightBold" fontSize={24} m={1}>
                Upload Data
            </Box>
            <div className={classes.controller}>
                <Button variant="outlined" component="label" className={classes.uploadBtn}>
                    <input type="file" style={{display:"none"}} accept="application/pdf" onChange={handleFile} />
                    {tempFile==null ? "" : " : "+ tempFile.name}
                </Button>
                {tempFile !== null ? <Button variant="outlined" color="primary" onClick={submitFile}>
                    <CachedIcon fontSize='small'/>&nbsp;&nbsp; 
                </Button> : null}
            </div>
        </div>

        <div className={classes.fileManager}>
            <Box fontWeight="fontWeightBold" fontSize={24} m={1}>
                  
            </Box>
            <div className={classes.headerRow}>
                    <div></div><div> </div><div> </div><div>  </div><div></div>
            </div>
            <div className={classes.fileList}>
                {fileList ? fileList.map((file, idx) => {
                    return (<>
                    <div className={classes.fileRow} onClick={() => loadDocList(file.id, file.file_name)}>
                        <div>{file.id}</div><div>{file.file_name}</div><div>{file.upload_time}</div><div>{file.complete_time}</div><div>{file.status}</div>
                    </div>
                    <div className={classes.rowDivider}/>
                    </>)
                }) : null}
            </div>
        </div>
        

        <Dialog open={loadingDialogOpen} aria-labelledby="upload-dialog" maxWidth='xs' fullWidth={true}>
            <DialogTitle id="upload-dialog">  OCR</DialogTitle>
            <List className={classes.dialogContent}>
                <ListItem>
                    <Typography variant="h6" noWrap >1)   &nbsp;&nbsp;</Typography>
                    {!uploadState.upload ? <CircularProgress size={20}/> : <DoneIcon />}
                </ListItem>
                <ListItem>
                    <Typography variant="h6" noWrap >2) OCR  &nbsp;&nbsp;&nbsp;</Typography>
                    {!uploadState.ocr ? <CircularProgress size={20}/> : <DoneIcon />}
                </ListItem>
            </List>
        </Dialog>

        <Dialog open={fileDialogOpen} className={classes.fileDialog} aria-labelledby="file-dialog" maxWidth={false} fullWidth={true}>
            <div className={classes.fileDescription}>
                <Box fontWeight="fontWeightBold" fontSize={24} m={1} style={{display: 'flex'}}>
                      
                    <Button className={classes.closeDialogBtn} onClick={() => setFileDialogOpen(false)}></Button>
                </Box>
                
                <div className={classes.dialogHeaderRow}>
                        <div>ID</div><div>TYPE</div><div>CODE</div><div>CREATE_TIME</div><div>LAST_UPDATE_TIME</div><div>STATUS</div>
                </div>
                <div className={classes.docList}>
                    {docList ? docList.WPS_List ? docList.WPS_List.map((doc, idx) => {
                        return (<>
                        <div className={classes.docRow}>
                            <div>{doc.id}</div><div>WPS</div><div>{doc.code_type}</div><div>{doc.upload_time}</div><div>{doc.inspect_time}</div><div>{doc.status}</div><div>{showBtn(doc.status, doc.id)}</div>
                        </div>
                        <div className={classes.rowDivider}/>
                        </>)
                    }) : null : null}
                    {docList ? docList.PQR_List ? docList.PQR_List.map((doc, idx) => {
                        return (<>
                        <div className={classes.docRow}>
                            <div>{doc.id}</div><div>PQR</div><div>{doc.code_type}</div><div>{doc.upload_time}</div><div>{doc.inspect_time}</div><div>{doc.status}</div><div>{showBtn(doc.status, doc.id)}</div>
                        </div>
                        <div className={classes.rowDivider}/>
                        </>)
                    }) : null : null}
                </div>
            </div>
        </Dialog>
        </>
    );
}